*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: hsl(217, 100%, 97%);
  font-family: "Hanken Grotesk", sans-serif;
  font-family: "Open Sans", sans-serif;
  font-family: "Roboto", sans-serif;
  font-family: "Ubuntu", sans-serif;
}

input {
  text-decoration: none;
}

.container {
  padding: 1rem;
  margin: 10rem auto 5rem;
  width: fit-content;
  background-color: hsl(0, 0%, 100%);
  display: flex;
  align-items: stretch;
  gap: 5rem;
  border-radius: 0.7rem;
  box-shadow: 0 0.25rem 0.25rem hsl(231, 11%, 63%);
}
.container .side-bar {
  background-image: url("../assets/images/bg-sidebar-desktop.svg");
  width: 274px;
  height: 568px;
  border-radius: 0.7rem;
  color: hsl(0, 0%, 100%);
  padding: 3rem 0 0 1.5rem;
}
.container .side-bar .steps {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.container .side-bar .steps .stp {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.container .side-bar .steps .stp .step-order {
  display: block;
  width: 2.5rem;
  height: 2.5rem;
  border: 0.1rem solid hsl(0, 0%, 100%);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
}
.container .side-bar .steps .stp .step-order.active {
  background-color: hsl(228, 100%, 84%);
  color: hsl(243, 100%, 62%);
}
.container .side-bar .steps .stp span {
  color: hsl(231, 11%, 63%);
  font-size: small;
}
.container .side-bar .steps .stp h2 {
  font-size: large;
  letter-spacing: 0.05rem;
}
.container .main-bar {
  padding: 3rem 4rem 0 0;
  position: relative;
}
.container .main-bar .btn {
  width: 7rem;
  height: 2.5rem;
  border-radius: 0.5rem;
  font-weight: 600;
  transition: 0.3s;
  position: absolute;
  bottom: 1rem;
  cursor: pointer;
}
.container .main-bar .btn:hover, .container .main-bar .btn:focus {
  outline: none;
}
.container .main-bar .btn.next, .container .main-bar .btn.submit {
  background-color: hsl(213, 96%, 18%);
  color: hsl(0, 0%, 100%);
  border: 0.1rem solid currentColor;
  right: 4rem;
}
.container .main-bar .btn.next:hover, .container .main-bar .btn.next:focus, .container .main-bar .btn.submit:hover, .container .main-bar .btn.submit:focus {
  background-color: transparent;
  color: hsl(213, 96%, 18%);
  outline: none;
}
.container .main-bar .btn.back {
  background-color: transparent;
  border: 0.1rem solid transparent;
  color: hsl(213, 96%, 18%);
}
.container .main-bar .step {
  display: none;
  flex-direction: column;
  width: 506.9px;
}
.container .main-bar .step .step-title {
  color: hsl(213, 96%, 18%);
  font-weight: 600;
  margin-bottom: 0.5rem;
}
.container .main-bar .step .step-dicrp {
  color: hsl(231, 11%, 63%);
}
.container .main-bar .step .form {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
}
.container .main-bar .step.current {
  display: flex;
}
.container .main-bar .step#step-1 .form {
  gap: 0.5rem;
}
.container .main-bar .step#step-1 .form label {
  color: hsl(213, 96%, 18%);
  font-weight: 500;
  font-size: small;
  margin-block: 1.25rem 0.25rem;
  position: relative;
}
.container .main-bar .step#step-1 .form label span {
  position: absolute;
  right: 0;
  color: hsl(354, 84%, 57%);
  font-weight: 600;
}
.container .main-bar .step#step-1 .form input {
  height: 2.5rem;
  border-radius: 0.5rem;
  border: 0.1rem solid hsl(229, 24%, 87%);
  padding-left: 1rem;
  font-weight: 600;
  color: hsl(213, 96%, 18%);
  caret-color: hsl(213, 96%, 18%);
  font-size: 0.95rem;
}
.container .main-bar .step#step-1 .form input:focus {
  outline: 0.1rem solid hsl(213, 96%, 18%);
}
.container .main-bar .step#step-1 .form input.error {
  border-color: hsl(354, 84%, 57%);
}
.container .main-bar .step#step-1 .form input.error:focus {
  outline: 0.1rem solid hsl(354, 84%, 57%);
}
.container .main-bar .step#step-2 .plans {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  margin-block: 2rem;
}
.container .main-bar .step#step-2 .plans .plan {
  width: 8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 0.1rem solid hsl(229, 24%, 87%);
  padding: 1rem 0.7rem;
  border-radius: 0.5rem;
  height: 168.15px;
  width: 150px;
  cursor: pointer;
}
.container .main-bar .step#step-2 .plans .plan:hover, .container .main-bar .step#step-2 .plans .plan.active {
  border-color: hsl(243, 100%, 62%);
}
.container .main-bar .step#step-2 .plans .plan.active {
  background-color: hsl(231, 100%, 99%);
}
.container .main-bar .step#step-2 .plans .plan h2 {
  font-size: medium;
  margin-block: 2rem 0.5rem;
  color: hsl(213, 96%, 18%);
}
.container .main-bar .step#step-2 .plans .plan span {
  font-size: small;
  color: hsl(231, 11%, 63%);
}
.container .main-bar .step#step-2 .plans .plan span.offer {
  color: hsl(213, 96%, 18%);
  font-weight: 500;
  padding-top: 0.5rem;
}
.container .main-bar .step#step-2 .options {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: hsl(231, 100%, 99%);
  padding-block: 0.5rem;
  border-radius: 0.5rem;
  gap: 0.7rem;
}
.container .main-bar .step#step-2 .options .switch {
  width: 2.5rem;
  height: 1.4rem;
  background-color: hsl(213, 96%, 18%);
  border-radius: 50px;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: 0.3s;
  position: relative;
}
.container .main-bar .step#step-2 .options .switch span {
  position: absolute;
  width: 0.85rem;
  height: 0.85rem;
  border-radius: 50%;
  background-color: hsl(0, 0%, 100%);
  transform: translateX(30%);
}
.container .main-bar .step#step-2 .options .switch span.yearly {
  transform: translateX(160%);
}
.container .main-bar .step#step-2 .options .option {
  font-size: medium;
}
.container .main-bar .step#step-2 .options * {
  transition: 0.3s;
  color: hsl(231, 11%, 63%);
}
.container .main-bar .step#step-2 .options *.active {
  color: hsl(213, 96%, 18%);
}
.container .main-bar .step#step-3 .add-ons {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.container .main-bar .step#step-3 .add-ons .service {
  border: 0.1rem solid hsl(229, 24%, 87%);
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  gap: 1.3rem;
  padding: 1.3rem;
  cursor: pointer;
}
.container .main-bar .step#step-3 .add-ons .service:hover, .container .main-bar .step#step-3 .add-ons .service:focus {
  border-color: hsl(243, 100%, 62%);
}
.container .main-bar .step#step-3 .add-ons .service.active {
  border-color: hsl(243, 100%, 62%);
  background-color: hsl(231, 100%, 99%);
}
.container .main-bar .step#step-3 .add-ons .service.active .check {
  background-color: hsl(243, 100%, 62%);
}
.container .main-bar .step#step-3 .add-ons .service .check {
  width: 1.35rem;
  height: 1.35rem;
  border: 1px solid hsl(229, 24%, 87%);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.2rem;
}
.container .main-bar .step#step-3 .add-ons .service .text {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.container .main-bar .step#step-3 .add-ons .service .text .service-name h4 {
  color: hsl(213, 96%, 18%);
}
.container .main-bar .step#step-3 .add-ons .service .text .service-name p {
  font-size: 0.92rem;
  color: hsl(231, 11%, 63%);
  margin-top: 0.3rem;
}
.container .main-bar .step#step-3 .add-ons .service .text .pricing {
  color: hsl(243, 100%, 62%);
  font-size: 0.9rem;
  font-weight: 500;
}
.container .main-bar .step#step-4 .selected-plan {
  margin-top: 2rem;
  background-color: hsl(217, 100%, 97%);
  border-radius: 0.5rem;
  flex-direction: column;
}
.container .main-bar .step#step-4 .selected-plan .plan {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  align-items: center;
  position: relative;
  color: hsl(213, 96%, 18%);
}
.container .main-bar .step#step-4 .selected-plan .plan #change {
  margin-top: 1rem;
  color: hsl(243, 100%, 62%);
  cursor: pointer;
  font-weight: 500;
}
.container .main-bar .step#step-4 .selected-plan .plan #change:hover {
  text-decoration: underline;
}
.container .main-bar .step#step-4 .selected-plan .plan .price {
  color: currentColor;
  font-weight: 600;
  font-size: 1.2rem;
}
.container .main-bar .step#step-4 .selected-plan .plan::before {
  content: "";
  position: absolute;
  bottom: 0;
  width: 90%;
  height: 0.01rem;
  left: 5%;
  background-color: hsl(213, 96%, 18%);
  opacity: 0.2;
}
.container .main-bar .step#step-4 .selected-plan .services {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  gap: 1rem;
}
.container .main-bar .step#step-4 .selected-plan .services .service {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.container .main-bar .step#step-4 .selected-plan .services .service h4 {
  opacity: 0.3;
}
.container .main-bar .step#step-4 .selected-plan .services .service .price {
  color: hsl(213, 96%, 18%);
  font-weight: 500;
  opacity: 0.7;
}
.container .main-bar .step#step-4 .selected-plan .total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: hsl(0, 0%, 100%);
}
.container .main-bar .step#step-4 .selected-plan .total h4 {
  opacity: 0.3;
}
.container .main-bar .step#step-4 .selected-plan .total .price {
  color: hsl(243, 100%, 62%);
  font-size: 1.4rem;
  font-weight: 600;
}